import { StatusBar } from 'expo-status-bar';
import React, { useState } from 'react';
import { StyleSheet, Text, View, SafeAreaView, ScrollView, TouchableOpacity, Image } from 'react-native';
import { useMediaQuery } from 'react-responsive';
import Section from './sections/Section';

export default function App() {
  const isMobile = useMediaQuery({ maxWidth: 767 }); // Use 767px as a breakpoint for mobile view
  const [isMenuOpen, setMenuOpen] = useState(false);

  const sectionsData = [
    {
      id: 1,
      title: 'Motores',
      text: 'Todas las marcas',
      image: require('./assets/logo.png'),
    },
    {
      id: 2,
      title: 'Refacciones',
      text: 'Los mejores precios',
      image: require('./assets/logo.png'),
    },
    {
      id: 3,
      title: 'Accesorios',
      text: 'La mejor variedad',
      image: require('./assets/logo.png'),
    },
  ];

  const scrollToSection = (sectionId) => {
    const yOffset = -80; // Adjust this offset if needed to offset the height of the fixed navbar
    const sectionElement = document.getElementById(sectionId);
    if (sectionElement) {
      const y = sectionElement.getBoundingClientRect().top + window.pageYOffset + yOffset;
      window.scrollTo({ top: y, behavior: 'smooth' });
    }
  };

  return (
    <SafeAreaView style={styles.container}>
      <ScrollView>
        <View style={styles.navbar}>
          <Image source={require('./assets/logo.png')} style={styles.logo} />
          {isMobile ? (
            <>
              {isMenuOpen && (
                <View style={styles.menu}>
                  <TouchableOpacity
                    style={styles.menuItem}
                    onPress={() => {
                      setMenuOpen(false);
                      scrollToSection('section1');
                    }}
                  >
                    <Text style={styles.menuItemText}>Motores</Text>
                  </TouchableOpacity>
                  <TouchableOpacity
                    style={styles.menuItem}
                    onPress={() => {
                      setMenuOpen(false);
                      scrollToSection('section2');
                    }}
                  >
                    <Text style={styles.menuItemText}>Refacciones</Text>
                  </TouchableOpacity>
                  <TouchableOpacity
                    style={styles.menuItem}
                    onPress={() => {
                      setMenuOpen(false);
                      scrollToSection('section3');
                    }}
                  >
                    <Text style={styles.menuItemText}>Accesorios</Text>
                  </TouchableOpacity>
                </View>
              )}
              <TouchableOpacity
                style={styles.menuButton}
                onPress={() => setMenuOpen((prev) => !prev)}
              >
                <Text style={styles.menuButtonText}>Menu</Text>
              </TouchableOpacity>
            </>
          ) : (
            <View style={styles.menu}>
              <TouchableOpacity
                style={styles.menuItem}
                onPress={() => scrollToSection('section1')}
              >
                <Text style={styles.menuItemText}>Motores</Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={styles.menuItem}
                onPress={() => scrollToSection('section2')}
              >
                <Text style={styles.menuItemText}>Refacciones</Text>
              </TouchableOpacity>
              <TouchableOpacity
                style={styles.menuItem}
                onPress={() => scrollToSection('section3')}
              >
                <Text style={styles.menuItemText}>Accesorios</Text>
              </TouchableOpacity>
            </View>
          )}
        </View>
        {isMobile ? (
          <>
            {sectionsData.map((section) => (
              <Section
                key={section.id}
                id={`section${section.id}`}
                title={section.title}
                text={section.text}
                image={section.image}
                isMobile={true}
              />
            ))}
          </>
        ) : (
          <View style={styles.webLayout}>
            {sectionsData.map((section) => (
              <View key={section.id} style={styles.webRow}>
                <View style={styles.webColumn}>
                  <Section
                    id={`section${section.id}`}
                    title={section.title}
                    text={section.text}
                    image={section.image}
                    isMobile={false}
                  />
                </View>
              </View>
            ))}
          </View>
        )}
        <StatusBar style="auto" />
      </ScrollView>
    </SafeAreaView>
  );
}

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#fff',
    paddingHorizontal: 20,
    paddingTop: 40,
  },
  navbar: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  logo: {
    width: 100, // Adjust the width of the logo as needed
    height: 40, // Adjust the height of the logo as needed
  },
  menuButton: {
    backgroundColor: '#f2f2f2',
    padding: 10,
    borderRadius: 5,
  },
  menuButtonText: {
    fontSize: 16,
  },
  menu: {
    flexDirection: 'row',
    marginTop: 10,
  },
  menuItem: {
    padding: 10,
    marginLeft: 10,
    display: 'inline-block',
  },
  menuItemText: {
    fontSize: 16,
  },
  webLayout: {
    gap: '20px',
  },
  webRow: {
    display: 'grid',
    gridTemplateColumns: '1fr 1fr',
    gap: '20px',
  },
  webColumn: {
    width: '50vw',
  },
});


























